/**
 * file-transfer-service.js --
 *
 * Base class for upload & download service.
 *
 */

import MKSVchan from '../channels/mksvchan';
import errorDialogTemplate from '../../common/commondialog/dialog_service_error.html';

export default function fileTransferService(AB, ngDialog, wmksService, $timeout,
                                            _) {
   function Service() {
      this.fileList = [];
      this.isTransferring = false;
      this.onTransferFinish = null;
      this.transferProgess = 0;
      this.dialog = null;
   }

   Service.prototype.openErrorDialog =
      function(errorDgName, errorMsg) {
         if (!!this.dialog && ngDialog.isOpen(this.dialog.id)) {
            AB.LOGGER.debug('Only open one dialog each time.');
            this.dialog.close();
         }

         this.dialog = ngDialog.open({
            name: errorDgName,
            templateUrl: errorDialogTemplate,
            className: "session-ops-dialog",
            data: {
               errorMessage: errorMsg,
               title: _('FT_WARNING_T'),
               buttonLabel: _('OK')
            },
            showClose: false,
            closeByEscape: true,
            closeByDocument: true
         });
      };

   Service.prototype.init = function(fileList, onTransferFinish) {
      if (!fileList || typeof onTransferFinish !== 'function') {
         AB.LOGGER.error(
            'Cannot init file transfer service due to param error!');
         return;
      }

      this.transferProgess = 0;
      for (let i = 0; i < fileList.length; i++) {
         if (fileList[i].progress === 100) {
            this.transferProgess++;
         }
      }
      this.fileList = fileList;
      this.onTransferFinish = onTransferFinish;
   };

   Service.prototype.isTransferringFile = function() {
      if (this.isTransferring) {
         this.openErrorDialog('BeingTransferWarningDialog',
            _('FT_BEING_TRANSFER_WARNING_M'));
         return true;
      }
      return false;
   };

   Service.prototype.isTransferringFileWithNoDialog = function() {
      return this.isTransferring;
   };

   Service.prototype.checkFileSize = function() {
      let res = false;

      for (let i = 0; i < this.fileList.length; i++) {
         if (this.fileList[i].size >=
            MKSVchan.FILE_TRANSFER_MAX_FILE_SIZE ||
            this.fileList[i].size === 0) {
            this.fileList[i].transferError = true;
            res = true;
         }
      }
      return res;
   };

   Service.prototype.checkFolderExist = function() {
      let res = false;

      for (let i = 0; i < this.fileList.length; i++) {
         if (this.fileList[i].fileType ===
            MKSVchan.FILE_TRANSFER_FILE_TYPE.DIRECTORY) {
            this.fileList[i].transferError = true;
            res = true;
         }
      }
      return res;
   };

   Service.prototype.checkSymlinkExist = function() {
      let res = false;

      for (let i = 0; i < this.fileList.length; i++) {
         if (this.fileList[i].fileType ===
            MKSVchan.FILE_TRANSFER_FILE_TYPE.SYMLINK) {
            this.fileList[i].transferError = true;
            res = true;
         }
      }
      return res;
   };

   return {
      baseService: new Service()
   };

}



