/**
 * dialog-accessibility.js
 *
 */

import JSCDKLogger from '../../jscdk/jscdkLogger';

export function dialogTabindex() {
   /**
    * this derective is serve to fix a bug when using native tabindex, so
    * have the similar interface.
    */
   return {
      require: '^dialogTabScope',
      scope: {},
      restrict: 'A',
      link: function(scope, element, attrs, scopeCtrl) {
         if (!scopeCtrl.inited) {
            scopeCtrl.init();
         }
         //add this element under control
         scopeCtrl.addChoosableElement(element, attrs.dialogTabindex);
      }
   };
}

export function dialogPreventKeyUp($rootScope) {
   /**
    * When dialog is closed, the focus could go back to header
    * element. And at this time it is possible that user is still
    * holding the key and then the the key up event is received.
    * That's not what we want.
    */
   return {
      restrict: 'A',
      link: function(scope, element) {
         element.keydown(function(event) {
            if (event.keyCode === 13) {
               $rootScope.isDialogServiceKeyPress = true;
            }
         });
      }
   };
}

export function dialogTabScope($timeout) {
   /**
    * this derective will create a isolated scope and controll the reaction
    * for
    * "tab" key pressing for all registered element in it.
    */
   return {
      restrict: 'A',
      scope: {},
      controller: ['$rootScope', '$scope', '$element',
         function($rootScope, $scope, $element) {
            this.inited = false;
            this.reseted = true;
            this.choosableArray = [];
            this.currentTabIndex = -1;

            this.reset = function() {
               this.reseted = true;
               this.currentTabIndex = -1;
            };

            this.isInnerElement = function(parent, child) {
               return parent.contains ?
                  parent !== child && parent.contains(child) :
                  !!(parent.compareDocumentPosition(child) & 16);
            };

            /**
             * return a auto assigned index as the default index
             */
            this.getDefaultIndex = function() {
               let i,
                  startIndex = 0,
                  indexSlot = startIndex;

               for (i = 0; i < this.choosableArray.length; i++) {
                  if (this.choosableArray[i].index >= startIndex) {
                     if (indexSlot === this.choosableArray[i].index) {
                        indexSlot++;
                     } else {
                        break;
                     }
                  }
               }
               return indexSlot;
            };

            /**
             * add a choosable element under control
             */
            this.addChoosableElement = function(element, index) {
               let i,
                  indexNumber,
                  item;

               if (!index) {
                  indexNumber = this.getDefaultIndex(index);
               } else {
                  indexNumber = Number(index);
               }

               item = {
                  index: indexNumber,
                  element: element
               };

               if (this.choosableArray.length === 0) {
                  this.choosableArray.push(item);
                  return;
               }

               if (this.findIndex(index) >= 0) {
                  JSCDKLogger.debug(
                     'repeat index is not allowed');
                  return;
               }

               for (i = 0; i < this.choosableArray.length; i++) {
                  if (this.choosableArray[i].index > index) {
                     //insert at index with item
                     this.choosableArray.splice(i, 0, item);
                     return;
                  }
               }

               this.choosableArray.push(item);
            };

            this.findIndex = function(targetTabIndex) {
               let i;
               if (targetTabIndex === -1) {
                  return -1;
               }
               for (i = 0; i < this.choosableArray.length; i++) {
                  if (this.choosableArray[i].index ===
                     targetTabIndex) {
                     return i;
                  }
               }
               return -1;
            };

            this.getNextElementIndex = function() {
               return (this.findIndex(this.currentTabIndex) + 1) %
                  this.choosableArray.length;
            };

            this.onTabDown = () => {
               $timeout(() => {
                  let nextElementIndex;

                  if (this.choosableArray.length === 0) {
                     JSCDKLogger.debug(
                        'the dialog-tabindex list is empty');
                     return;
                  }
                  nextElementIndex = this.getNextElementIndex();
                  this.choosableArray[nextElementIndex].element[0].focus();
                  this.currentTabIndex = this.choosableArray[
                     nextElementIndex].index;
               });
            };

            this.onKeyDown = function(event) {
               if (event.keyCode === 9) {
                  JSCDKLogger.trace('tab key pressed');
                  event.preventDefault();
                  this.onTabDown();
               }
            };

            this.focusOnDefault = function() {
               let defaultElement = this.choosableArray[0];
               if (!defaultElement) {
                  JSCDKLogger.debug(
                     'the default element to be focused don\'t exist'
                  );
                  return;
               }
               defaultElement.element[0].focus();
               this.currentTabIndex = defaultElement.index;
            };

            this.init = function() {
               let onFocus,
                  onFocusOut,
                  workingDialog,
                  children = $element.children(),
                  keyDownFunc = this.onKeyDown.bind(this);

               // valid check
               if (!children || !children[0]) {
                  return;
               }
               workingDialog = children[0];

               //callbacks definition, binding "this" is for referring the
               // this.choosableArray and this.currentTabIndex
               onFocus = function(event) {
                  if (this.reseted) {
                     // set focus on the default button
                     this.focusOnDefault();
                     JSCDKLogger.debug(
                        'dialog focus reseted to default');
                     this.reseted = false;
                  }
               }.bind(this);

               onFocusOut = function(event) {
                  if (this.isInnerElement($element[0], event.target) &&
                     !this.isInnerElement($element[0], event.relatedTarget)
                  ) { //dialog loss focus
                     this.reset();
                  }
               }.bind(this);

               //callbacks bind
               $element.on('keydown', keyDownFunc);
               $(workingDialog).focus(onFocus);
               $(workingDialog).focusout(onFocusOut);

               //callbacks unbind when destructed
               $element.on('$destroy', () => {
                  $element.off('keydown', keyDownFunc);
                  $(workingDialog).off('focus', onFocus);
                  $(workingDialog).off('focusout',
                     onFocusOut);
                  this.inited = false;
                  this.choosableArray = [];
                  this.currentTabIndex = -1;
               });

               this.inited = true;
            };
         }]
   };
}




