import $ from 'jquery';
import JSCDKLogger from '../../jscdk/jscdkLogger';

export default function timeoutService($window, connectionServerModel,
                                       storageService,
                                       jscdkLauncherInvoker) {
   let userActivityTime = 0;

   this.setUserActivityTime = function() {
      userActivityTime = new Date().getTime();
   };

   this.getLastUserActivityTime = function() {
      return userActivityTime;
   };

   this.bindUserActivity = () => {
      this.setUserActivityTime();
      // Listen to all the events for tracking user activity.
      $(document).bind(
         'mousemove.useractivity mousedown.useractivity mouseup.useractivity',
         this.setUserActivityTime);
      $(document).bind('keydown.useractivity keyup.useractivity',
         this.setUserActivityTime);
      $(document).bind(
         'touchstart.useractivity touchmove.useractivity touchend.useractivity',
         this.setUserActivityTime);
      $(document)
         .bind('orientationchange.useractivity', this.setUserActivityTime);
      $(document)
         .bind('extendedmonitoruse.useractivity', this.setUserActivityTime);
      $(document)
         .bind('primarymonitoruse.useractivity', this.setUserActivityTime);
   };

   this._readTimerFromCookieToConnectionServerModel = function() {
      let idleTimeout,
         userActivityXMLSendingInterval;

      // Current idleTimeout value
      idleTimeout = connectionServerModel.idleTimeout;
      if (!idleTimeout) {
         // Read idleTimeout from cookie
         idleTimeout = storageService.cookie.read(
            storageService.cookie.COOKIE_IDLE_TIMEOUT);
         if (!idleTimeout) {
            JSCDKLogger.debug(
               'error: idleTimeout don\'t exist in the cookie');
         }
         // Cache idleTimeout in the server model.
         connectionServerModel.idleTimeout = idleTimeout;
      } else {
         JSCDKLogger.debug(
            'warning: idleTimeout already exist in the connectionServerModel');
      }
      // Current user-activity-interval value
      userActivityXMLSendingInterval =
         connectionServerModel.userActivityXMLSendingInterval;
      if (!userActivityXMLSendingInterval) {
         // Read user-activity-interval value from cookie
         userActivityXMLSendingInterval = storageService.cookie.read(
            storageService.cookie.COOKIE_SEND_TIME_INTERVAL);
         if (!userActivityXMLSendingInterval) {
            JSCDKLogger.debug(
               'error: userActivityXMLSendingInterval don\'t exist in the cookie');
         }
         // Cache user-activity-interval value in the server model.
         connectionServerModel.userActivityXMLSendingInterval =
            userActivityXMLSendingInterval;
      } else {
         JSCDKLogger.debug(
            'warning: userActivityXMLSendingInterval already exist in the connectionServerModel');
      }
   };

   // name the interface with clear name instead of flag
   this.initTimers = function() {
      this._readTimerFromCookieToConnectionServerModel();
      jscdkLauncherInvoker.sendInitTimerXML();
   };

   this.initIntervalTimer = function() {
      this._readTimerFromCookieToConnectionServerModel();
      jscdkLauncherInvoker.sendInitTimerXML(true);
   };
}




