import sharedUtil from '../common/module';
import primaryMonitorService from './primary-monitor-service';
import multimonService from './multimon-service';
import multimonModel from './multimon-model';
import monitorManageService from './monitor-manage-service';
import extendedMonitorService from './extended-monitor-service';
import ExtendedMonitor from './extended-monitor';
import multimonRenderingService from './multimon-rendering-service';

export default angular.module('com.vmware.vdi.htmlaccess.desktop.multimon',
   [sharedUtil])
   .service("primaryMonitorService", ["jQuery",
      "$window",
      "$timeout",
      "fullscreenService",
      "wmksBaseService",
      "vncDecoder",
      "normalizationService",
      primaryMonitorService
   ])
   .service("multimonService", ["monitorManageService",
      "multimonModel",
      "dialogService",
      "$timeout",
      "translate",
      multimonService
   ])
   .service("multimonModel", ["ExtendedMonitor",
      "primaryMonitorService",
      multimonModel
   ])
   .service("monitorManageService", ["$timeout",
      "$window",
      "$rootScope",
      "multimonModel",
      "extendedMonitorService",
      "normalizationService",
      "dialogService",
      "vncDecoder",
      "renderCacheService",
      "translate",
      monitorManageService
   ])
   .service("multimonRenderingService", ["monitorManageService",
      multimonRenderingService
   ])
   .service("extendedMonitorService", ["$window", "jQuery", "translate",
      extendedMonitorService
   ])
   .service("ExtendedMonitor", ["$window", "extendedMonitorService",
      "vncDecoder", "$timeout",
      ExtendedMonitor
   ])
   .name;


