import JSCDKLogger from '../../jscdk/jscdkLogger';

export default function dpiService($window, constants, userGlobalPref) {
   let logger = {
      info: function (msg) {
         JSCDKLogger.info('[dpiService] ' + msg);
      },
      error: function (msg) {
         JSCDKLogger.error('[dpiService] ' + msg);
      },
      debug: function (msg) {
         JSCDKLogger.debug('[dpiService] ' + msg);
      }
   };

   return {
      getDPI: function () {
         let dpi;
         if(userGlobalPref.getPrefData().enableDPISync !== 'true') {
            dpi = parseFloat(constants.DPI_100_PERCENT *
               $window.devicePixelRatio).toString();
         } else {
            logger.info("DPI Sync is disabled.  " +
               "Ignore the real client DPI");
            dpi = '96';
         }
         logger.info("System DPI is " + dpi);
         return dpi;
      }
   };
}




