import $ from 'jquery';

export default function jscdkDesktopInvoker($rootScope, $location, $timeout,
                                            $window, viewClientModel,
                                            userGlobalPref, dpiService,
                                            connectionServerModel,
                                            constants, timezone) {

   this.jscdkHandler = null;

   this._invokeJSCDK = action => {
      // TODO: Move to jscdkUIAdapter later
      this.jscdkHandler.invokeJSCDK(action);
   };


   this.resetItem = (itemId, isDesktop) => {
      let killItemAction = {};
      killItemAction.method = "KillSession";
      killItemAction.itemInfo = {
         id: itemId,
         isDesktop: isDesktop
      };
      this.jscdkHandler.invokeJSCDK(killItemAction);
   };

   this.resetDesktop = desktopId => {
      const resetDesktopAction = {
         method: "ResetDesktop",
         desktopId: desktopId
      };
      this.jscdkHandler.invokeJSCDK(resetDesktopAction);
   };

   this.restartDesktop = desktopId => {
      const restartDesktopAction = {
         method: "RestartDesktop",
         desktopId: desktopId
      };
      this.jscdkHandler.invokeJSCDK(restartDesktopAction);
   };

   this.logout = () => {
      const logoutAction = {
         method: 'LogoutFromBroker',
         prefData: userGlobalPref.toFlatFormat()
      };
      this.jscdkHandler.invokeJSCDK(logoutAction);
   };

   this.submitWindowsPassword = (username, password,
                                 domain, reAuth) => {
      const submitAuthInfoAction = {
         method: 'SubmitAuthInfo',
         type: 'WindowsPassword',
         isDesktopReAuth: true,
         username: username,
         secret: password,
         domain: domain
      };
      if (reAuth) {
         submitAuthInfoAction.reAuth = true;
      }

      this.jscdkHandler.invokeJSCDK(submitAuthInfoAction);
   };

   this.changePassword = (oldPassword, newPassword1,
                          newPassword2) => {
      const submitAuthInfoAction = {
         method: 'SubmitAuthInfo',
         isDesktopReAuth: true,
         type: 'WindowsPasswordExpired'
      };
      submitAuthInfoAction.oldPassword = oldPassword;
      submitAuthInfoAction.newPassword1 = newPassword1;
      submitAuthInfoAction.newPassword2 = newPassword2;

      this.jscdkHandler.invokeJSCDK(submitAuthInfoAction);
   };

   this.cancelCurrentRequest = doneAction => {
      this.jscdkHandler.invokeJSCDK({
         method: 'CancelCurrentRequest',
         doneAction: doneAction
      });
   };

   this.sendLaunchItemsXML = () => {
      const getLaunchItemsAction = {
         method: 'GetLaunchItems',
         skipTunnelAndPref: true,
         skipTimer: true
      };
      this.jscdkHandler.invokeJSCDK(getLaunchItemsAction);
   };

   this.getUserGlobalPref = () => {
      this.jscdkHandler.invokeJSCDK({
         method: "GetUserGlobalPref"
      });
   };

   this.disconnectApplicationSession = applicationsession => {
      this.jscdkHandler.invokeJSCDK({
         method: "DisconnectSession",
         launchItemId: applicationsession.id
      });
   };

   this.resetAllApplicationSessions = () => {
      let resetApplicationSessionAction = {};
      resetApplicationSessionAction.method = 'KillSession';
      resetApplicationSessionAction.killAllApplicationSessions = true;
      this.jscdkHandler.invokeJSCDK(resetApplicationSessionAction);
   };

   this.reconnectApplicationSessions =
      (targetSessionIds, excludedOriginalId) => {
         let reconnectApplicationSessionsAction = {};
         reconnectApplicationSessionsAction.method = "ReconnectSession";
         reconnectApplicationSessionsAction.targetSessionIds =
            targetSessionIds;
         reconnectApplicationSessionsAction.disconnectAll = false;
         reconnectApplicationSessionsAction.excludedOriginalId =
            excludedOriginalId;
         reconnectApplicationSessionsAction.environmentInfo =
            this._getEnvironmentInfo(false);

         this.jscdkHandler.invokeJSCDK(reconnectApplicationSessionsAction);
      };

   this.launchApplication = appInfo => {
      let application = appInfo.application,
         args = appInfo.args,
         selectApplicationAction = {};

      // connect to application
      selectApplicationAction.method = 'ConnectToApplication';
      /*
       * launch application for the specific protocol
       * currently, only BLAST is allowed.
       */
      selectApplicationAction.protocol =
         connectionServerModel.connectionProtocol;
      selectApplicationAction.applicationId = application.id;
      selectApplicationAction.originId =
         application['origin-id'];
      selectApplicationAction.maximized = 'false';
      selectApplicationAction.environmentInfo =
         this._getEnvironmentInfo(false);

      selectApplicationAction.preferences = {
         applicationName: application.name,
         resolution: {
            'x': 0,
            'y': 0,
            'w': $(window).width(),
            'h': $(window).height()
         }
      };
      if (!!args) {
         selectApplicationAction.launchParam = {
            "args": args
         };
      }

      selectApplicationAction.prefData =
         userGlobalPref.toFlatFormat();
      this.jscdkHandler.invokeJSCDK(selectApplicationAction);
   };

   this.launchDesktop = desktop => {
      let selectDesktopAction = {},
         ratio = 1;

      selectDesktopAction.method = 'ConnectToDesktop';
      /* launch desktop for the specific protocol
       * currently, only BLAST is allowed.
       */
      selectDesktopAction.protocol =
         connectionServerModel.connectionProtocol;
      selectDesktopAction.desktopId = desktop.id;
      selectDesktopAction.isShadow = desktop.isShadow;
      if (!desktop.isShadow) {
         selectDesktopAction.environmentInfo =
            this._getEnvironmentInfo(true);
      }

      selectDesktopAction.preferences = {
         desktopName: desktop.name,
         resolution: {
            'x': 0,
            'y': 0,
            'w': $(window).width() * ratio,
            'h': $(window).height() * ratio
         }
      };

      selectDesktopAction.prefData =
         userGlobalPref.toFlatFormat();
      this.jscdkHandler.invokeJSCDK(selectDesktopAction);
   };

   this.connectToBroker = () => {
      let connectBrokerAction = {},
         clientCommonInfo,
         clientStats,
         addClientCommonInfo,
         addClientStats,
         userAgent = navigator.userAgent,
         isHandheldDevice = !!userAgent.match(
            /(iPad)|(iPhone)|(iPod)|(android)|(IEMobile)|(webOS)/i),
         matches,
         browserName = 'unknown',
         browserVersion,
         browserArch = navigator.platform;

      // Connect To the Broker
      connectBrokerAction.method = 'ConnectToBroker';
      connectBrokerAction.address = connectionServerModel.host;
      connectBrokerAction.supportedProtocols =
         connectionServerModel.supportedProtocols;

      // //ignore ISO for now
      // if (!!connectionURIModel.isF5Session()) {
      //    connectBrokerAction.mid = connectionURIModel.getF5postFix();
      // }

      if (userAgent.toLowerCase().match(/(iphone|ipod|ipad)/i)) {
         // Apple iOS6 introduced a new xml post bug where the browser
         // would cache response if the request data did not change.
         // Hack to make sure we always request a new url, by adding a
         // timestamp whenever get an iOS browser
         connectBrokerAction.urlParam = '?_ab=' + $.now();
      }

      // Match pattern like 'Chrome/26.0.1410.64' to retrieve browser
      // name and version.
      matches = userAgent.match(
         /(opera|chrome|safari|firefox|msie|trident)\/?\s*(\.?\d+(\.\d+)*)/i);
      if (!!matches) {
         browserName = matches[1];
         browserVersion = matches[2];
      }

      // For Microsoft Edge, the useragent is like:
      // "Mozilla/5.0 (Windows NT 10.0; WOW64) AppleWebKit/537.36
      // (KHTML, like Gecko) Chrome/39.0.2171.71 Safari/537.36
      // Edge/12.0". Match pattern Edge, because there's a "chrome"
      // string in it.
      if (userAgent.toLowerCase().indexOf('edge')) {
         matches = userAgent.match(/(edge)\/?\s*(\.?\d+(\.\d+)*)/i);
         if (!!matches) {
            browserName = matches[1];
            browserVersion = matches[2];
         }
      }

      if (browserName === 'Safari') {
         // For Safari, the version is in 'Version/6.0.3'.
         matches = userAgent.match(/version\/([\.\d]+)/i);
         if (!!matches) {
            browserVersion = matches[1];
         }
      } else if (browserName === 'Trident') {
         // For IE 11, the version is in 'rv:11.0'.
         browserName = 'Internet Explorer';
         matches = userAgent.match(/rv[:\s]([\w.]+)/i);
         if (!!matches) {
            browserVersion = matches[1];
         }
      }
      // For Chromebook, detect browser-arch from userAgent text.
      matches = userAgent.match(/cros\s*[a-z0-9_\.]*/i);
      if (!!matches) {
         browserArch = matches[0];
      }

      // Client common info
      clientCommonInfo = {
         // 'VMware'
         'client-vendor': viewClientModel.vendor,
         // 'VMware Horizon HTML Access'
         'client-product': viewClientModel.product,
         // View client version
         'client-version': viewClientModel.version + '-' +
         viewClientModel.buildNum,
         // 'Chrome Apps'
         'client-arch': viewClientModel.arch
      };
      // Client platform specific info
      clientStats = {
         // Browser info
         'browser-user-agent': userAgent,
         // Browser core
         'browser-core': browserName,
         // Browser version
         'browser-version': browserVersion,
         // System info
         'browser-arch': browserArch,
         // Is mobile device
         'browser-is-handheld': isHandheldDevice ? 'true' : 'false'
      };

      addClientCommonInfo = {
         'entity-type': 'CLIENT',
         'data-type': 'configuration'
      };

      addClientStats = {
         // the same with windows client
         'entity-id': 'singleton'
      };

      connectBrokerAction.clientInfo = {
         'clientCommonInfo': clientCommonInfo,
         'clientStats': clientStats,
         'addClientCommonInfo': addClientCommonInfo,
         'addClientStats': addClientStats,
      };

      this.jscdkHandler.invokeJSCDK(connectBrokerAction);
   };

   this.sendDoUnlock = () => {
      const sendDoUnlockAction = {
         method: 'DoUnlock'
      };
      this.jscdkHandler.invokeJSCDK(sendDoUnlockAction);
   };

   this.authenticationStatusCheck = (callback, ondone) => {
      const authStatusCheckAction = {
         method: 'GetAuthenticationStatus',
         workForSilentMode: true
      };

      this.jscdkHandler.invokeJSCDK(authStatusCheckAction);
   };

   this.setEnvironmentInfo = function(environmentInfo) {
      const setEnvironmentInfoAction = {
            "method": "SetEnvironmentInfo",
            "data": environmentInfo
         };
      this.jscdkHandler.invokeJSCDK(setEnvironmentInfoAction);
   };

   this._getEnvironmentInfo = isDesktop => {
      let environmentInfo = {};

      if (!!viewClientModel.ipAddress) {
         environmentInfo['IP_Address'] =
            viewClientModel.ipAddress;
         environmentInfo['Machine_Name'] =
            viewClientModel.machineName;
      }
      if (!!viewClientModel.clientID) {
         environmentInfo['Client_ID'] = viewClientModel.clientID;
      }
      environmentInfo['Client_Version'] = viewClientModel.version + '-' +
         viewClientModel.buildNum;
      environmentInfo['TZID'] = timezone.getWindowsTimezone();
      environmentInfo['Type'] = constants.viewclientType;

      environmentInfo['Displays.SystemDpi'] =
         dpiService.getDPI();

      return environmentInfo;
   };
}



