import angular from 'angular';

export default function prefData() {
   let data = {
      favorites: [], //favorites is an array
      isHighResolution: '',
      useMacOSXKeyMappings: '',
      enableWindowsKey: false,

      reset: function() {
         this.favorites = [];
         this.isHighResolution = '';
         this.useMacOSXKeyMappings = '';
         this.enableWindowsKey = false;
         this.timezoneSync = '';
      },

      equals: function(data) {
         try {
            if (this.favorites.sort().toString() ===
               data.favorites.sort().toString() &&
               this.isHighResolution === data.isHighResolution &&
               this.useMacOSXKeyMappings === data.useMacOSXKeyMappings &&
               this.enableWindowsKey === data.enableWindowsKey &&
               this.timezoneSync === data.timezoneSync) {
               return true;
            } else {
               return false;
            }
         } catch (e) {
            return false;
         }
      },

      toJSON: function() {
         let r = {};

         angular.forEach(this, function(value, index) {
            if (typeof value !== 'function') {
               if (index === 'favorites') {
                  angular.forEach(value, function(value, index) {
                     this['favorite' + index] = value;
                  }, r);
               } else {
                  this[index] = value;
               }
            }
         }, r);
         return r;
      }
   };

   return data;
}




