import timezoneListTemplate from './time-zone-list.html';


export function timezoneListDirective() {
   return {
      replace: true,
      restrict: 'E',
      templateUrl: timezoneListTemplate
   };
}

export function timezone($window, $log, prefData) {
   return {
      /*
       * https://wiki.eng.vmware.com/VDM/ClientBrokerXml#
       * TimeOffset_GMT : -07:00
       * TZID : Europe/London
       * Windows_Timezone: GMT Standard Time
       */
      timeZoneMap: {
         "-12:00": "Etc/GMT-12",
         "-11:00": "Etc/GMT-11",
         "-10:00": "Pacific/Honolulu",
         "-09:30": "Pacific/Marquesas",
         "-09:00": "America/Anchorage",
         "-08:00": "America/Los_Angeles",
         "-07:00": "America/Denver",
         "-06:00": "America/Chicago",
         "-05:00": "America/New_York",
         "-04:00": "America/Asuncion",
         "-03:30": "America/St_Johns",
         "-03:00": "America/Sao_Paulo",
         "-02:00": "America/Noronha",
         "-01:00": "Atlantic/Azores",
         "-00:00": "Europe/London",
         "+01:00": "Europe/Budapest",
         "+02:00": "Europe/Kiev",
         "+03:00": "Europe/Moscow",
         "+03:30": "Asia/Tehran",
         "+04:00": "Asia/Dubai",
         "+04:30": "Asia/Kabul",
         "+05:00": "Asia/Tashkent",
         "+05:30": "Asia/Calcutta",
         "+05:45": "Asia/Katmandu",
         "+06:00": "Asia/Almaty",
         "+06:30": "Asia/Rangoon",
         "+07:00": "Asia/Bangkok",
         "+08:00": "Asia/Shanghai",
         "+08:30": "Asia/Pyongyang",
         "+08:45": "Australia/Eucla",
         "+09:00": "Asia/Tokyo",
         "+09:30": "Australia/Darwin",
         "+10:00": "Australia/Sydney",
         "+10:30": "Australia/Lord_Howe",
         "+11:00": "Etc/GMT+11",
         "+12:00": "Pacific/Fiji",
         "+12:45": "Pacific/Chatham",
         "+13:00": "Tonga",
         "+14:00": "Pacific/Honolulu"
      },

      isDaylightSaving: function() {
         Date.prototype.stdTimezoneOffset = function() {
            let jan = new Date(this.getFullYear(), 0, 1);
            let jul = new Date(this.getFullYear(), 6, 1);
            return Math.max(jan.getTimezoneOffset(),
               jul.getTimezoneOffset());
         };

         Date.prototype.dst = function() {
            return this.getTimezoneOffset() < this.stdTimezoneOffset();
         };

         let today = new Date();
         return !!today.dst();
      },

      getTimezoneOffsetData: function() {
         let offset, r, h;
         offset = new $window.Date().getTimezoneOffset();

         if (this.isDaylightSaving()) {
            offset = offset + 60;
         }

         r = offset < 0 ? '+' : '-';
         offset = offset < 0 ? (0 - offset) : offset;

         offset = String(offset / 60).split('.');
         h = offset[0].length > 1 ? offset[0] : '0' + offset[0];
         r += h;
         r += ":";

         r += offset.length > 1 ? '30' : '00';

         /**
          * As we want to keep remote desktop time the same with local,
          * so  don't consider daylight saving now. Bug 1730455
          */
         $log.info("Timezone offset: " + r);
         return r;
      },

      getWindowsTimezone: function() {
         if (!prefData.timezoneSync) {
            let timezoneOffset = this.getTimezoneOffsetData();
            let WindowsTimezone = this.timeZoneMap[timezoneOffset];
            $log.info("Auto get Timezone data: " + WindowsTimezone);
            return WindowsTimezone;
         } else {
            $log.info(
               "Prefer timezone user preference: " + prefData.timezoneSync);
            return prefData.timezoneSync;
         }
      }

   };
}




