import angular from 'angular';
import {fullscreenSubService, fullscreenService} from './fullscreen-service';
import bitBuffer from './bitbuffer';
import renderCacheService from './render-cache-service';
import vncDecoder from './vnc-decoder';
import wmksBaseService from './wmks-base-service';
import normalizationService from './normalization-service';


export default angular.module('com.vmware.vdi.htmlaccess.desktop.sharedUtil', [])
   .service('fullscreenSubService', ['$window', fullscreenSubService])
   .service('bitBuffer', bitBuffer)
   .service('renderCacheService', ['bitBuffer', renderCacheService])
   .service('normalizationService', normalizationService)
   .service('fullscreenService', ['$window', '$timeout', 'fullscreenSubService', fullscreenService])
   .service('vncDecoder', ['normalizationService', 'renderCacheService', vncDecoder])
   .service('wmksBaseService', ['$window', 'normalizationService', wmksBaseService])
   .name;


