/**
 * file-transfer-helper.js --
 *
 * Common helper for both ft and print service.
 *
 */

import MKSVchan from '../channels/mksvchan';
import AB from '../common/appblast-util';

export default function TransferHelper(wmksService, FTDownloadService,
                                       FTUploadService,
                                       wmksServiceEventHandler) {

   function Service(consumer) {
      if (consumer !== MKSVchan.FILE_TRANSFER_CONSUMER.FT &&
         consumer !== MKSVchan.FILE_TRANSFER_CONSUMER.PRINT) {
         throw 'Wrong type of consumer is used!';
      }

      AB.LOGGER.debug("Init transfer helper");
      this.consumer = consumer;
   }

   /**
    * Update the download file list.
    * Since it is possible that some file in the coming list has
    * already been added, we need to be careful and verify if it is
    * added. See bug 1645572.
    */

   Service.prototype.updateDownloadFileList = function(fileList,
                                                       client) {
      /**
       * $scope.downloadFiles is referred to
       * mksClient.FTUtil.downloadList[FT/PRINT]. We need to use the
       * later one, in case some bugs while desktop switch.
       * See bug 1755174, 1755201, 1756222.
       */
      if (!client || !client.FTUtil) {
         AB.LOGGER.error("Cannot find FTUtil for this desktop!");
         return;
      }

      let isAdded,
         downloadList = client.FTUtil.downloadList[this.consumer];
      for (let i = 0; i < fileList.length; i++) {
         isAdded = false;
         for (let j = 0; j < downloadList.length; j++) {
            if (fileList[i].fullPath ===
               downloadList[j].fullPath &&
               fileList[i].uuid === downloadList[j].uuid
            ) {
               isAdded = true;
               break;
            }
         }

         if (isAdded === false) {
            downloadList.push(fileList[i]);
         }
      }
   };

   /**
    * Used to get current mks client with transfer service.
    * @param consumer
    * @returns {*}
    */
   Service.prototype.initTransferService = function() {
      let mksClient = wmksService.getCurrentMKSVchanClient();
      if (mksClient == null) {
         return null;
      }

      // Only file transfer has upload service now
      if (!mksClient.uploadService &&
         this.consumer === MKSVchan.FILE_TRANSFER_CONSUMER.FT) {
         mksClient.uploadService = FTUploadService
            .getService(mksClient);
      }
      if (!mksClient
            .downloadServices[this.consumer]) {
         mksClient.downloadServices[this.consumer] =
            FTDownloadService.getService(mksClient, this.consumer);
      }

      return mksClient;
   };

   Service.prototype.addStatusListener = function(func) {
      if (typeof func !== "function") {
         AB.LOGGER.info("Invalid parameter");
         return;
      }

      wmksServiceEventHandler.addEventListener(
         "fileTransferConfigChange",
         function(error) {
            func();
         }
      );

      // Listen to session change
      wmksServiceEventHandler.addEventListener('activeSessionChanged',
         func);
      // Listen to new session
      wmksServiceEventHandler.addEventListener('sessionConnected',
         func);

      if (this.consumer === MKSVchan.FILE_TRANSFER_CONSUMER.FT) {
         wmksServiceEventHandler.listenerReadyForModule(
            wmksServiceEventHandler.MODULE_NAME.FT_HELPER);
      } else if (this.consumer ===
         MKSVchan.FILE_TRANSFER_CONSUMER.PRINT) {
         wmksServiceEventHandler.listenerReadyForModule(
            wmksServiceEventHandler.MODULE_NAME.PRINT_HELPER);
      }
   };

   return {
      getInstance: function(consumer) {
         return new Service(consumer);
      }
   };
}



