import angular from 'angular';
import commonModule from '../common/module';
import ngRoute from 'angular-route';
import ngDialog from 'ng-dialog';

import routeConfig from './route';

import LauncherSettingsWindowController from './setting-dialog/setting-dialog-controller';
import LaunchItemsCtrlService from './launchitems/itemsctrl-service';
import LaunchItemsCtrl from './launchitems/items-controller';
import viewCtrlService from './common/viewctrl-service';
import viewService from './common/view-service';
import {
   equalToDirective,
   serverNameDirective
} from './common/validate-directive';
import utilService from './common/util-service';
import timeoutService from './common/timeout-service';
import tabSyncService from './common/tabsync-service';
import mainService from './common/main-service';
import jscdkLauncherInvoker from './common/jscdk-launcher-invoker';
import HomeWarningCtrl from './common/homewarning-controller';
import {HeaderCtrl, headerDirective} from './common/header-controller';
import {
   scrollTopDirective,
   viewAutofocusDirective,
   unstorablePasswordDirective,
   fallbackSrcDirective,
   progressCircleDirective,
   helpLinkDirective,
   downloadLinkdirective,
   highlightDirective
} from './common/common-directive';
import BlastDesktopCtrl from './common/blastdesktop-controller';
import WindowsPasswordCtrl from './login/windowspassword-controller';
import UnauthenticatedCtrl from './login/unauthenticated-controller';
import SecuridWaitCtrl from './login/securidwait-controller';
import SecuridPinChangeCtrl from './login/securidpinchange-controller';
import SecuridPasscodeCtrl from './login/securidpasscode-controller';
import SecuridNextTokenCodeCtrl from './login/securidnexttokencode-controller';
import {LoginCtrl, WaitingCtrl} from './login/login-controller';
import domainSelectMenuDirective from './login/domain-select-menu-directive';
import DisclaimerCtrl from './login/disclaimer-controller';
import ChangePasswordCtrl from './login/changepassword-controller';
import anonymousSelectDirective from './login/anonymous-select-directive';


export default angular.module('com.vmware.vdi.htmlaccess.launcher',
   [ngRoute, ngDialog, commonModule])
   .config(['$routeProvider', routeConfig])
   .controller('LauncherSettingsWindowController',
      ['$scope', '$rootScope', 'utilService', 'userGlobalPref', 'translate',
         'dialogService', 'mainService', 'viewClientModel', 'prefData',
         'timezone', '$timeout', 'jscdkLauncherInvoker',
         LauncherSettingsWindowController])
   .controller('LaunchItemsCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'viewClientModel', 'mainService', 'storageService',
         'LaunchItemsCtrlService', 'utilService', 'safeStorageService',
         'userGlobalPref', 'translate', 'connectionURIModel', 'dialogService',
         'credCleanService', 'timezone', 'jscdkLauncherInvoker',
         'brokerSessionStorageService', 'constants', LaunchItemsCtrl])
   .controller('HomeWarningCtrl',
      ['$scope', 'utilService', 'mainService', 'viewClientModel',
         'storageService', HomeWarningCtrl])
   .controller('HeaderCtrl',
      ['$rootScope', '$scope', 'connectionServerModel', 'utilService',
         'dialogService', HeaderCtrl])
   .controller('BlastDesktopCtrl',
      ['$window', '$scope', '$rootScope', 'mainService',
         'connectionServerModel',
         'blastSessionsModel', 'viewClientModel',
         'connectionURIModel', 'multiSiteService', 'viewService', 'constants',
         'userGlobalPref', 'baseViewService', 'jscdkLauncherInvoker',
         BlastDesktopCtrl])
   .controller('WindowsPasswordCtrl',
      ['$scope', 'connectionServerModel', 'mainService',
         'utilService', 'translate', 'dialogService', 'viewClientModel',
         'connectionURIModel', 'viewService', 'usernameUtil',
         'jscdkLauncherInvoker',
         WindowsPasswordCtrl])
   .controller('UnauthenticatedCtrl',
      ['$scope', 'connectionServerModel', 'translate', 'viewService',
         'mainService', 'jscdkLauncherInvoker',
         'connectionURIModel', 'utilService', 'dialogService',
         UnauthenticatedCtrl])
   .controller('SecuridWaitCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'mainService', 'utilService', 'jscdkLauncherInvoker',
         SecuridWaitCtrl])
   .controller('SecuridPinChangeCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'mainService', 'utilService', 'jscdkLauncherInvoker', 'translate',
         SecuridPinChangeCtrl])
   .controller('SecuridPasscodeCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'mainService', 'utilService', 'jscdkLauncherInvoker', 'translate',
         SecuridPasscodeCtrl])
   .controller('SecuridNextTokenCodeCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'mainService', 'utilService', 'jscdkLauncherInvoker', 'translate',
         SecuridNextTokenCodeCtrl])
   .controller('LoginCtrl', ['$scope', '$rootScope', 'mainService',
      'utilService', 'storageService', 'jscdkLauncherInvoker', LoginCtrl])
   .controller("WaitingCtrl", ['$scope', '$rootScope', WaitingCtrl])
   .controller('DisclaimerCtrl',
      ['$scope', '$rootScope', '$timeout', 'mainService', 'utilService',
         'jscdkLauncherInvoker', DisclaimerCtrl])
   .controller('ChangePasswordCtrl',
      ['$scope', '$timeout', 'mainService', 'utilService',
         'connectionServerModel', 'jscdkLauncherInvoker',
         ChangePasswordCtrl])
   .factory('LaunchItemsCtrlService',
      ['connectionServerModel',
         'mainService', 'utilService', 'dialogService', 'safeStorageService',
         'translate', 'connectionURIModel',
         'viewService', 'jscdkLauncherInvoker', LaunchItemsCtrlService])
   .service('viewCtrlService', ['$rootScope', '$timeout', viewCtrlService])
   .service('viewService', ['$location', '$timeout', 'utilService',
      'connectionURIModel', 'tabSyncService', 'baseViewService',
      'URIParamValidator', 'dialogService', 'jscdkLauncherInvoker', 'translate',
      'constants', 'ws1Service', viewService])
   .service('utilService', ['connectionURIModel', 'translate',
      'dialogService', 'localize', 'brokerSessionStorageService',
      'viewClientModel', 'jscdkLauncherInvoker', '$window',
      utilService])
   .service('timeoutService', ['$window', 'connectionServerModel',
      'storageService', 'jscdkLauncherInvoker', timeoutService])
   .service("tabSyncService", ['$window', 'utilService', tabSyncService])
   .service('mainService', ['$rootScope', 'utilService', '$route',
      'dialogService', 'viewClientModel', 'connectionURIModel',
      'translate', 'viewService', 'timeoutService',
      'userGlobalPref', 'brokerSessionStorageService', 'viewCtrlService',
      'jscdkLauncherInvoker', 'constants', '$window', 'ws1Service',
      mainService])
   .service('jscdkLauncherInvoker', ['$window', 'viewClientModel',
      'connectionURIModel', 'userGlobalPref', 'dpiService',
      'storageService', 'connectionServerModel', 'constants',
      jscdkLauncherInvoker])
   .directive('equalTo', equalToDirective)
   .directive('serverName', serverNameDirective)
   .directive('scrollTop', scrollTopDirective)
   .directive('viewAutofocus', ['$timeout', viewAutofocusDirective])
   .directive('unstorablePassword', ['$timeout', unstorablePasswordDirective])
   .directive('fallbackSrc', fallbackSrcDirective)
   .directive('progressCircle', progressCircleDirective)
   .directive('helpLink', ['viewClientModel', 'utilService', 'translate',
      '$timeout', '$rootScope', 'localize', helpLinkDirective])
   .directive('downloadLink', ['translate',
      '$timeout', '$rootScope', 'viewClientModel', 'localize',
      downloadLinkdirective])
   .directive('highlight', highlightDirective)
   .directive('domainSelectMenu', ['$timeout', 'viewClientModel',
      domainSelectMenuDirective])
   .directive('anonymousSelect', ['$timeout', anonymousSelectDirective])
   .directive('launcherHeader', headerDirective)
   .name;



