export default function URIParamValidator(usernameUtil) {

   this.isValid = function(URIParams) {
      if (!URIParams) {
         // If there is no URI params, that is valid as well
         return true;
      }

      // userName=test@domain1&domainName=domain2
      if (usernameUtil.domainInUsername(URIParams.userName) &&
         !!URIParams.domainName) {
         let domain = usernameUtil.getDomain(URIParams.userName);
         if (domain.toLowerCase() !== URIParams.domainName.toLowerCase()) {
            return false;
         }
      }

      // TODO more params check here

      return true;
   };

}



