export default function model($rootScope, $timeout, brokerSessionStorageService, constants) {
   function BaseViewClientModel() {
      // Name of company that produced this client
      this.vendor = 'VMware';

      // Architecture of client
      this.arch = '';

      // Vendor-specific client-type identifier
      this.product = 'VMware Horizon HTML Access';

      // Product version
      this.version = '';

      // unique client ID
      this.clientID = '';

      this.linkModel = {
         // Help Pages, now Unique ID
         'helpPages': {
            'en': 'https://docs.vmware.com/en/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'de': 'https://docs.vmware.com/de/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'es': 'https://docs.vmware.com/es/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'fr': 'https://docs.vmware.com/fr/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'ja': 'https://docs.vmware.com/jp/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'ko': 'https://docs.vmware.com/kr/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'zh-CN': 'https://docs.vmware.com/cn/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
            'zh-TW': 'https://docs.vmware.com/tw/VMware-Horizon-HTML-Access/4.6/'+
                  'com.vmware.horizon.html-access-46-user-guide/GUID-20F0C9F6-'+
                  '7DE9-4D3D-8095-391C9F795F54.html',
         },
         // go to forum Pages
         'gotoForumPages': {
            'en': 'https://communities.vmware.com/community/vmtn/horizon/view'
         },
         /*
          * EULAs of HTML Access are in the installer instead of online web pages.
          * Here are just some dummy IDs. Out-Of-Use
          */
         'eulaPages': {
            'en': 'https://www.vmware.com/info?id=1327'
         }

      };

      this.MIN_VERSION = {
         'IE': 10,
         'Firefox': 36,
         'Gecko': 21,
         'Chrome': 41,
         'Safari': 7,
         'WebKit': 536
      };

      this.acceptLanguage = null;
      this.clientVersion = null;
      this.logLevel = null;
      this.ipAddress = null;
      this.rebrandName = null;
      this.rebrandHelpUrl = '';
      this.rebrandGotoForumUrl = '';
      this.windowsTimezone = null;

      /**
       *  Features in 'client-config' of broker response
       */
      this.clientHideDomainList = !!brokerSessionStorageService
         .read('clientHideDomainList');
      this.clientHideServerInformation = !!brokerSessionStorageService
         .read('clientHideServerInformation');
      this.credentialCleanup = !!brokerSessionStorageService
         .read('credentialCleanup');
      this.isAnonymousMode = !!brokerSessionStorageService
         .read(constants.ANONYMOUS_MODE_KEY);
      $timeout(() => {
         $rootScope.showPreference = !this.isAnonymousMode;
      });
   }

   // Return constructor function.
   return BaseViewClientModel;
}




