/**
 * dialog-service.js
 *
 * The unified dialog-service for desktop/launcher module.
 * It is a simple wrapper for ngDialog. And ngDialog is pre-configured in
 * 'common/module.js'
 */

import angular from 'angular';
import $ from 'jquery';
import errorDialogTemplate from './dialog_service_error.html';
import confirmDialogTemplate from './dialog_service_confirm.html';
import cancelDialogTemplate from './dialog_service_cancel_confirm.html';


export function ngDialogProvider(ngDialogProvider) {
   ngDialogProvider.setDefaults({
      name: "DefaultDialog",
      className: "session-ops-dialog",
      showClose: false,
      closeByEscape: true,
      closeByDocument: true
   });
}

export function dialogService(ngDialog) {
   this._DEFAULT_ERROR_OPTIONS = {
      name: "DefaultErrorDialog",
      templateUrl: errorDialogTemplate
   };
   this._DEFAULT_CONFIRM_OPTOINS = {
      name: "DefaultConfirmDialog",
      templateUrl: confirmDialogTemplate
   };

   this.confirm = options => {
      if (!options) {
         options = {};
      }
      let defaultOptions = this._DEFAULT_CONFIRM_OPTOINS;
      angular.extend(defaultOptions, options);
      return ngDialog.open(defaultOptions);
   };

   this.confirmWithCancelAsDefault = function(options) {
      if (!options) {
         options = {};
      }
      let defaultOptions = {
         name: "ConfirmWithCancelAsDefault",
         templateUrl: cancelDialogTemplate
      };
      angular.extend(defaultOptions, options);
      return ngDialog.open(defaultOptions);
   };

   this.error = options => {
      if (!options) {
         options = {};
      }
      let defaultOptions = this._DEFAULT_ERROR_OPTIONS;
      angular.extend(defaultOptions, options);
      return ngDialog.open(defaultOptions);
   };

   this.predefined = function(options, isConfirmDialog) {
      if (isConfirmDialog) {
         return ngDialog.openConfirm(options);
      }
      return ngDialog.open(options);
   };

   this.isDialogOpen = function(dialogID) {
      return ngDialog.isOpen(dialogID);
   };

   this.hasDialogOpen = function() {
      let dialog = $('.ngdialog');
      return !(dialog === null || dialog.length === 0);
   };
}




