//should spoort highlighting both app and desktop, added to html later, then
// filename should also be changed

import $ from 'jquery';
import progressCircleTemplate from './progress-circle.html';

export function scrollTopDirective() {
   return {
      restrict: 'A',
      scope: {
         isHighlighted: '&scrollTop'
      },
      link: function(scope, elem, attrs) {
         // whehter this desktop is highlighted
         if (scope.isHighlighted()) {
            // scroll 10px to the top.
            $('html, body').animate({
               scrollTop: elem.offset().top - 10
            }, 150);
         }
      }
   };
}

export function viewAutofocusDirective($timeout) {
   return {
      link: function(scope, elem, attrs, ctrl) {

         function setFocus() {
            if (attrs.viewAutofocus === 'true') {
               $timeout(function() {
                  elem[0].focus();
               });
            }
         }

         attrs.$observe('viewAutofocus', setFocus);
      }
   };
}

export function unstorablePasswordDirective($timeout) {
   return {
      link: function(scope, elem, attrs) {
         $timeout(function() {
            $(elem[0]).prop('type', 'password');
         }, 200);
      }
   };
}

export function fallbackSrcDirective() {
   return {
      link: function postLink(scope, iElement, iAttrs) {
         if (!iAttrs.ngSrc) {
            iElement.addClass('default-icon');
         } else {
            iElement.bind('error', function() {
               $(this).addClass(iAttrs.fallbackSrc);
            });
         }
      }
   };
}

export function progressCircleDirective() {
   return {
      restrict: 'E',
      templateUrl: progressCircleTemplate
   };
}

export function helpLinkDirective(viewClientModel, utilService, _, $timeout, $rootScope,
         localize) {
   return {
      restrict: 'E',
      replace: true,
      link: function(scope, element) {
         let showLink = function() {
            $timeout(function() {
               //Rebrand the help URL
               let helpUrl = viewClientModel.rebrandHelpUrl ||
                     utilService.getLinkByType('helpPages'),
                  name = _('help_with_vmware_horizon');
               element.html(name);
               element.attr('href', helpUrl);
               scope.showHelpLink = true;
            });
         };

         $rootScope.$on("model:localizationReady", function(event,
                                                            data) {
            /**
             * In most cases,
             * localize.translate('help_with_vmware_horizon') does not
             * get the correct result because the resource is downloaded
             * asynchronously. model:localizationReady event is emitted
             * by localize service when the downloaded is finished using
             * $timeout to un-block the waiting widget UI using
             * '$rootScope' not 'scope' to register the event is for
             * performance consideration.  $emit is cheaper than
             * $broadcast
             */
            showLink();
         });

         if (localize.localizationReady) {
            showLink();
         }
      },
      template: '<a class="home-link" id="helpLink" href="" target="_blank" ng-show="showHelpLink"></a>'
   };
}

export function downloadLinkdirective(_, $timeout, $rootScope, viewClientModel, localize) {
   return {
      restrict: 'E',
      replace: true,
      link: function(scope, element) {
         let showLink = function() {
            $timeout(function() {
               //Rebrand the installer URL
               let name = _(viewClientModel.osLiteral);
               element.html(name);
               element.attr('href', viewClientModel.nativeInstallerLink);
               // For unit testing
               scope.nativeInstallerLink =
                  viewClientModel.nativeInstallerLink;
            });
         };

         $rootScope.$on("model:localizationReady", function(event,
                                                            data) {
            /**
             * In most cases,
             * localize.translate('help_with_vmware_horizon') does not
             * get the correct result because the resource is downloaded
             * asynchronously. model:localizationReady event is emitted
             * by localize service when the downloaded is finished using
             * $timeout to un-block the waiting widget UI using
             * '$rootScope' not 'scope' to register the event is for
             * performance consideration.  $emit is cheaper than
             * $broadcast
             */
            showLink();
         });

         if (localize.localizationReady) {
            showLink();
         }
      },
      template: '<a class="home-link" id="downloadLink" href="" target="_blank"></a>'
   };
}

export function highlightDirective() {
   return {
      restrict: 'A',
      link: function(scope, elem, attrs) {
         let toggle = function(enabled) {
            if (enabled) {
               elem.addClass('highlight');
            } else {
               elem.removeClass('highlight');
            }
         };

         toggle(scope[attrs.highlight]);
         scope.$watch(attrs.highlight, toggle);
      }
   };
}



