import $ from 'jquery';
import WMKS from 'WMKS';
import angular from 'angular';
import commonModule from '../common/module';
import multimonModule from './multimon/main-page/module';
import ngRoute from 'angular-route';
import ngDialog from 'ng-dialog';

import routeConfig from './route';

import MKSVchan from './channels/mksvchan';
import VDPService from './channels/vdpservice';
import Unity from './remoteapp/unity';
import AB from './common/appblast-util';
import RTAVService from './rtav/rtav-service';
import verticalSliderDirective from './sidebar/vertical-slider';
import {
   runningItemsFilter,
   availableItemsFilter,
   SidebarController,
   sidebarDirective
} from './sidebar/sidebar';
import contextMenuManager from './sidebar/context-menu-manager';
import settingsWindowController from './setting-dialog/settings-window-controller';
import unityService from './remoteapp/unity-service';
import reAuthCtrl from './re-auth/reauth-controller';
import reAuthChangePasswordCtrl from './re-auth/changepassword-controller';
import multimonSettingWindowController from './multimon/multimon-setting-window/multimon-setting-window-controller';
import FTUploadService from './filetransfer/file-transfer-upload-service';
import fileTransferService from './filetransfer/file-transfer-service';
import {
   fileTransferController,
   fileTransferPanel,
   fileTransferInput,
   fileDrop,
   fileChange
} from './filetransfer/file-transfer-panel';
import TransferHelper from './filetransfer/file-transfer-helper';
import FTDownloadService from './filetransfer/file-transfer-download-service';
import wmksServiceEventHandler from './common/wmks-service-event-handler';
import wmksService from './common/wmks-service';
import runningItemsModel from './common/runningitems-model';
import {PrintController, printFrame} from './common/print-service';
import panelService from './common/panel-service';
import messageHandlerService from './common/messagehandler-service';
import jscdkDesktopInvoker from './common/jscdk-desktop-invoker';
import idleSessionService from './common/idle-session-service';
import entitledItemsService from './common/entitleditems-service';
import entitledItemsModel from './common/entitleditems-model';
import DesktopCtrl from './common/desktop-controller';
import blastWmks from './common/blast-wmks-service';
import audioService from './common/audio-service';
import applicationSessionsModel from './common/applicationsessions-model';
import {
   ajaxBusyOverlayController,
   ajaxBusyOverlay
} from './common/ajax-busy-overlay';
import {
   clipboardController,
   clipboardInput,
   panelNotification,
   clipboard
} from './clipboard/clipboard';
import mainChannelService from './channels/rde-main-channel';
import tsdrChannelService from './channels/tsdr-channel';
import commonSvcService from './channels/commonSvc-service';

export default angular.module("com.vmware.vdi.htmlaccess.desktop",
   [ngDialog, ngRoute, commonModule, multimonModule])
   .constant('WMKS', WMKS)
   .constant('Unity', Unity)
   .constant('VDPService', VDPService)
   .constant('MKSVchan', MKSVchan)
   .constant('RTAVService', RTAVService)
   .constant('AB', AB)
   .constant('jQuery', $)
   .config(['$routeProvider', routeConfig])

   .factory("contextMenuManager", ['$window', contextMenuManager])
   .factory("unityService", ['$window', 'wmksService', 'Unity',
      'wmksServiceEventHandler', unityService])
   .factory('FTUploadService',
      ['AB', 'wmksService', '$timeout', 'translate', 'fileTransferService',
         FTUploadService
      ])
   .factory('fileTransferService',
      ['AB', 'ngDialog', 'wmksService', '$timeout', 'translate',
         fileTransferService
      ])
   .factory('TransferHelper',
      ['wmksService', 'FTDownloadService', 'FTUploadService',
         'wmksServiceEventHandler', TransferHelper])
   .factory('FTDownloadService',
      ['AB', 'wmksService', '$timeout', 'fileTransferService', 'translate',
         FTDownloadService
      ])
   .factory('wmksService', [
      'blastWmks',
      'safeStorageService',
      'entitledItemsModel',
      'entitledItemsService',
      'messageHandlerService',
      'MKSVchan',
      'WMKS',
      'AB',
      'jQuery',
      '$timeout',
      '$window',
      '$rootScope',
      'wmksServiceEventHandler',
      'multimonService',
      'commonSvcService',
      'userGlobalPref',
      wmksService
   ])
   .factory('panelService', ['$window', 'AB',
      panelService
   ])
   .factory("blastWmks", [
      '$window',
      'ngDialog',
      'audioService',
      'safeStorageService',
      'VDPService',
      'MKSVchan',
      'RTAVService',
      'multimonRenderingService',
      '$timeout',
      'WMKS',
      'AB',
      'jQuery',
      'translate',
      '$rootScope',
      '$q',
      'mainChannelService',
      'tsdrChannelService',
      blastWmks
   ])
   .factory("audioService", ['AB', '$window', 'WMKS', audioService])
   .factory("mainChannelService", mainChannelService)
   .factory("tsdrChannelService", tsdrChannelService)
   .factory("commonSvcService", ['AB', 'wmksServiceEventHandler',
      'dpiService', 'userGlobalPref', 'constants', commonSvcService])

   .filter("runningItemsFilter", runningItemsFilter)
   // Filter for available items given a search query string and whether only
   // favorites should be shown.
   .filter("availableItemsFilter", availableItemsFilter)
   .controller("SidebarController", [
      '$scope',
      '$window',
      '$rootScope',
      '$timeout',
      'unityService',
      'runningItemsModel',
      'contextMenuManager',
      'wmksService',
      'entitledItemsModel',
      'entitledItemsService',
      'userGlobalPref',
      'ngDialog',
      'messageHandlerService',
      'RTAVService',
      'translate',
      'WMKS',
      'idleSessionService',
      'wmksServiceEventHandler',
      'commonSvcService',
      'normalizationService',
      SidebarController
   ])
   .controller('settingsWindowController',
      ['$scope',
         '$rootScope',
         '$timeout',
         'wmksService',
         'userGlobalPref',
         'entitledItemsService',
         'ngDialog',
         'WMKS',
         'translate',
         settingsWindowController])
   .controller('reAuthCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'jscdkDesktopInvoker',
         'viewClientModel',
         'usernameUtil', 'entitledItemsService', 'ngDialog', reAuthCtrl])
   .controller('reAuthChangePasswordCtrl',
      ['$scope', '$rootScope', '$timeout', 'connectionServerModel',
         'jscdkDesktopInvoker', reAuthChangePasswordCtrl])
   .controller('multimonSettingWindowController', ['$scope',
      '$rootScope',
      '$timeout',
      'wmksService',
      'userGlobalPref',
      'ngDialog',
      'translate',
      multimonSettingWindowController
   ])
   .controller('fileTransferController', ['$window', '$scope',
      '$rootScope',
      '$timeout',
      'ngDialog',
      'wmksService',
      'WMKS',
      'TransferHelper',
      'translate',
      'toastService',
      'wmksServiceEventHandler',
      'panelService',
      fileTransferController
   ])
   .controller('PrintController', ['AB', '$scope',
      '$timeout',
      'WMKS',
      'wmksService',
      'translate',
      'TransferHelper',
      'toastService',
      'wmksServiceEventHandler',
      PrintController
   ])
   .controller('DesktopCtrl',
      ['$window', 'AB', '$scope', '$location', 'wmksService',
         'entitledItemsService', DesktopCtrl])
   .controller('ajaxBusyOverlayController', ['$scope', '$rootScope', '$timeout',
      ajaxBusyOverlayController])
   .controller('clipboardController',
      ['AB', '$scope',
         '$rootScope',
         '$timeout',
         'ngDialog',
         'wmksService',
         'translate',
         'wmksServiceEventHandler',
         'panelService',
         clipboardController])


   .directive('printFrame', ['$timeout', 'AB', 'WMKS',
      '$window', 'wmksService', 'toastService',
      printFrame])
   .directive('sidebar', sidebarDirective)
   .directive('fileTransferPanel',
      ['$window', 'panelService', '$timeout',
         fileTransferPanel
      ])
   .directive('fileTransferInput', fileTransferInput)
   .directive('fileDrop', fileDrop)
   .directive('fileChange', fileChange)
   .directive('verticalSlider',
      ['$window', 'AB', verticalSliderDirective])
   .directive('ajaxBusyOverlay', ajaxBusyOverlay)
   .directive('clipboardInput', clipboardInput)
   .directive('panelNotification', ['translate', panelNotification])
   .directive('clipboard',
      ['$window', 'panelService', '$timeout', clipboard])

   .service('wmksServiceEventHandler',
      ['$q', 'WMKS', 'AB', wmksServiceEventHandler])
   .service("runningItemsModel", ['AB', 'wmksService', 'unityService',
      '$rootScope', '$timeout', 'translate', 'wmksServiceEventHandler',
      runningItemsModel])
   .service("messageHandlerService", ['AB', '$window',
      messageHandlerService
   ])
   .service('jscdkDesktopInvoker',
      ['$rootScope', '$location', '$timeout', '$window', 'viewClientModel',
         'userGlobalPref', 'dpiService', 'connectionServerModel', 'constants',
         'timezone',
         jscdkDesktopInvoker])
   .service('idleSessionService', idleSessionService)
   .service("entitledItemsService", ['AB', 'safeStorageService',
      'entitledItemsModel',
      'applicationSessionsModel',
      'blastSessionsModel',
      'userGlobalPref',
      '$rootScope',
      '$q',
      'multiSiteService',
      'timezone',
      'translate',
      'constants',
      'idleSessionService',
      'dpiService',
      'jscdkDesktopInvoker',
      'ngDialog',
      'viewClientModel',
      'baseViewService',
      entitledItemsService])
   .service("entitledItemsModel", ["safeStorageService", "RTAVService",
      entitledItemsModel])
   .service("applicationSessionsModel", ["jscdkDesktopInvoker",
      "viewClientModel", "constants",
      applicationSessionsModel
   ])

   .name;




