/**
 * Common module which provides many useful services/models.
 */

import angular from 'angular';
import ngDialog from 'ng-dialog';
import usernameUtil from './service/username-util';
import userGlobalPref from './service/userglbpref';
import {localize, i18n, translate} from './service/localize-service';
import {timezone, timezoneListDirective} from './timezone/timezone';
import URIParamValidator from './service/uri-param-validator';
import toastService from './service/toast-service';
import storageService from './service/storage-service';
import safeStorageService from './service/safe-storage-service';
import prefData from './service/prefdata';
import multiSiteService from './service/multisite-service';
// import jscdkUIAdapter from './service/jscdk-ui-adapter';
import dpiService from './service/dpi-service';
import credCleanService from './service/credentialclean-service';
import brokerSessionStorageService from './service/broker-session-storage-service';
import baseViewService from './service/base-view-service';
import viewClientModel from './model/viewclient-model';
import osModel from './model/os-model';
import connectionURIModel from './model/connection-uri-model';
import connectionServerModel from './model/connection-server-model';
import clientIdFactory from './model/clientid-model';
import blastSessionsModel from './model/blastsessions-model';
import BaseViewClientModel from './model/baseviewclient-model';
import fuzzyFree from './directive/scrollable-dialog-fuzzy-free';
import scrollableDialog from './directive/scrollable-dialog';
import ws1Service from './service/ws1-service';
import {
   dialogTabindex,
   dialogPreventKeyUp,
   dialogTabScope
} from './directive/dialog-accessibility';
import constants from './constant/app-constant';
import {ngDialogProvider, dialogService} from './commondialog/dialog-service';
import AboutDialogCtrl from './aboutdialog/about-dialog-controller';


export default angular.module('com.vmware.vdi.htmlaccess.common', [ngDialog])
   .service('usernameUtil', usernameUtil)
   .service('userGlobalPref',
      ['$log', '$rootScope', '$window', 'prefData', userGlobalPref])
   .factory('localize', ['$window', 'viewClientModel', '$rootScope', localize])
   .filter('i18n', ['localize', i18n])
   .factory('translate', ['localize', translate])
   .factory('timezone', ['$window', '$log', 'prefData', timezone])
   .service('URIParamValidator', ['usernameUtil', URIParamValidator])
   .service('toastService', toastService)
   .service('storageService', ['$window', storageService])
   .service('safeStorageService', ['$window', safeStorageService])
   .factory('prefData', prefData)
   .service('multiSiteService', ['$window', multiSiteService])
   // .service('jscdkUIAdapter', ['$rootScope', '$q', '$window',
   // jscdkUIAdapter])
   .factory('dpiService',
      ['$window', 'constants', 'userGlobalPref', dpiService])
   .service('credCleanService',
      ['$window', 'userGlobalPref', 'viewClientModel', credCleanService])
   .service('brokerSessionStorageService',
      ['$window', 'storageService', brokerSessionStorageService])
   .service('baseViewService',
      ['$location', '$timeout', '$window', 'viewClientModel',
         'credCleanService', baseViewService])
   .factory('viewClientModel',
      ['$window', 'BaseViewClientModel', 'osModel', 'clientID',
         'storageService', viewClientModel])
   .factory('osModel', osModel)
   .service('connectionURIModel',
      ['storageService', 'usernameUtil', 'viewClientModel', connectionURIModel])
   .factory('connectionServerModel', ['$window', connectionServerModel])
   .factory('clientID', ['$window', clientIdFactory])
   .service('blastSessionsModel',
      ['brokerSessionStorageService', blastSessionsModel])
   .factory('BaseViewClientModel',
      ['$rootScope', '$timeout', 'brokerSessionStorageService', 'constants',
         BaseViewClientModel])
   .directive('fuzzyFree', ['$rootScope', '$timeout', fuzzyFree])
   .directive('scrollableDialog', ['$window', scrollableDialog])
   .directive('dialogTabindex', dialogTabindex)
   .directive('dialogPreventKeyUp', ['$rootScope', dialogPreventKeyUp])
   .directive('dialogTabScope', ['$timeout', dialogTabScope])
   .directive('timezoneList', timezoneListDirective)
   .constant('constants', constants)
   .config(['ngDialogProvider', ngDialogProvider])
   .service('dialogService', ['ngDialog', dialogService])
   .service('ws1Service', ['constants',
      'brokerSessionStorageService', 'connectionURIModel', ws1Service])
   .controller('AboutDialogCtrl',
      ['$scope', 'viewClientModel', 'localize', AboutDialogCtrl]).name;



