import angular from 'angular';
import toastr from 'toastr';

export default function toastService() {

   this.TOAST_TYPE = {
      DEFAULT: 0,
      PREDEFINED: 1,
      FT: 2,
      PRINT: 3
   };

   this.LEVEL = {
      INFO: 'info',
      SUCCESS: 'success',
      WARNING: 'warning',
      ERROR: 'error'
   };

   this.COMMON_OPTIONS = {
      "closeButton": false,
      "debug": false,
      "newestOnTop": false,
      "progressBar": false,
      "positionClass": "toast-top-center",
      "preventDuplicates": false,
      "onclick": null,
      "showDuration": "300",
      "hideDuration": "1000",
      "timeOut": "5000",
      "extendedTimeOut": "1000",
      "showEasing": "swing",
      "hideEasing": "linear",
      "showMethod": "fadeIn",
      "hideMethod": "fadeOut"
   };
   this.DEFAULT_OPTIONS = {};
   this.PRINT_OPTIONS = {};
   this.FT_OPTIONS = {};
   angular.copy(this.COMMON_OPTIONS, this.DEFAULT_OPTIONS);
   angular.copy(this.COMMON_OPTIONS, this.PRINT_OPTIONS);
   angular.copy(this.COMMON_OPTIONS, this.FT_OPTIONS);
   this.PRINT_OPTIONS.timeOut = "0";
   this._currentToast = null;
   this._toastArray = [];

   toastr.options = this.DEFAULT_OPTIONS;

   this.setOptions = function(options) {
      if (!options) {
         return;
      }
      toastr.options = options;
   };

   this._showToast = function(level, text, type) {
      if (!text) {
         return;
      }

      if (level !== this.LEVEL.INFO && level !== this.LEVEL.ERROR &&
         level !== this.LEVEL.WARNING && level !== this.LEVEL.SUCCESS) {
         throw 'Error level type is used';
      }

      if (!type) {
         type = this.TOAST_TYPE.DEFAULT;
      }

      let optionCache = toastr.options;
      switch (type) {
         case this.TOAST_TYPE.DEFAULT: {
            toastr.options = this.DEFAULT_OPTIONS;
            this._currentToast = toastr[level](text);
            break;
         }
         case this.TOAST_TYPE.PREDEFINED: {
            this._currentToast = toastr[level](text);
            break;
         }
         case this.TOAST_TYPE.FT: {
            toastr.options = this.FT_OPTIONS;
            this._currentToast = toastr[level](text);
            toastr.options = optionCache;
            break;
         }
         case this.TOAST_TYPE.PRINT: {
            toastr.options = this.PRINT_OPTIONS;
            this._currentToast = toastr[level](text);
            toastr.options = optionCache;
            break;
         }
         default: {
            throw 'Error msg type is used.';
         }
      }
      this._toastArray.push({
         toast: this._currentToast,
         type: type
      });
   };

   this.success = function(text, type) {
      this._showToast(this.LEVEL.SUCCESS, text, type);
   };

   this.warning = function(text, type) {
      this._showToast(this.LEVEL.WARNING, text, type);
   };

   this.error = function(text, type) {
      this._showToast(this.LEVEL.ERROR, text, type);
   };

   this.info = function(text, type) {
      this._showToast(this.LEVEL.INFO, text, type);
   };

   this.clearCurrentToast = function() {
      if (!!this._currentToast) {
         toastr.clear(this._currentToast);
      }
   };

   this.clearEarlyToast = function(type) {
      if (this._toastArray.length < 1) {
         return;
      }

      if (!type) {
         type = this.TOAST_TYPE.DEFAULT;
      }

      let i = 0;
      while (i < this._toastArray.length) {
         if (this._toastArray[i].type === type) {
            toastr.clear(this._toastArray[i].toast);
            this._toastArray.splice(i, 1);
            continue;
         }
         i++;
      }
   };

   this.clearAll = function() {
      toastr.clear();
   };
}




