/**
 * extended-monitor-service.js -- extendedMonitorService
 *
 * provide data
 * extendedMonitorService
 */

import JSCDKLogger from '../../../jscdk/jscdkLogger';

export default function extendedMonitorService($window, $, _) {
   let getTranslateMap = function() {
      let keys = ["MM_WAIT_OTHER_MONITOR_READY",
            "MM_HINT_MOVE",
            "MM_SELECT_MONITOR",
            "MM_QUIT_MULTIMON_T",
            "MM_QUIT_MULTIMON_M",
            "OK",
            "CANCEL",
            "MM_MONITOR_DETECTOR"
         ],
         translationMap = {};

      //user promise if translationService allow.
      for (let i = 0; i < keys.length; i++) {
         translationMap[keys[i]] = _(keys[i]);
      }
      return translationMap;
   };

   this.statusMap = {
      opened: 0,
      inited: 1,
      readyToDisplay: 2,
      displaying: 3,
      close: -1,
      confirmedQuit: -2
   };

   this.wmksSession = null; //TODO: should be set outside
   this.translationMap = getTranslateMap();
   this.onMouseButton = function(data) {
      this.wmksSession.wmksContainer.wmks('sendMouseButtonMessage',
         data.position,
         data.isDownEvent, data.bmask);
      $(document).trigger("extendedmonitoruse");
   };
   this.onMouseMove = function(data) {
      this.wmksSession.wmksContainer.wmks('sendMouseMoveMessage',
         data.position);
      $(document).trigger("extendedmonitoruse");
   };
   this.onMouseWheel = function(data) {
      this.wmksSession.wmksContainer.wmks('sendScrollMessage', data.position,
         data.dx,
         data.dy);
      $(document).trigger("extendedmonitoruse");
   };
   this.onKeyEvent = function(data) {
      this.wmksSession.wmksContainer.wmks('sendKeyMessage', data);
      $(document).trigger("extendedmonitoruse");
   };
   this.onBlur = function(data) {
      JSCDKLogger.debug("focus leave extended monitor");
      this.wmksSession.wmksContainer.wmks('onBlur');
      $(document).trigger("extendedmonitoruse");
   };
}




