/**
 * multimon-service.js -- multimonService
 *
 * The top level service of multimon which provide 2 APIs:
 *  - addMonitor
 *  - switchToSingleMon
 */

import JSCDKLogger from '../../../jscdk/jscdkLogger';

export default function multimonService(monitorManageService, multimonModel,
   dialogService, $timeout, _) {
   let onQuitUICallback = null;
   this.onTopologyChanged = () => {
      /*if(!settings valid) {
       return;
       }*/
      //TODO, to support ad hoc monitor modify, need to add code here
      this.sendTopologyRequest();
   };

   this.onQuitMultimon = () => {
      JSCDKLogger.info("quit multimon");
      // clear hint here if PM want to
      this.uiCallback("onQuitMultimon");
   };

   this.onEnterMultimon = () => {
      JSCDKLogger.info("enter multimon");
      // pop out hint here if PM want to
      this.closeSidebar();
      this.uiCallback("onEnterMultimon");
   };

   this.onSingleMonitor = () => {
      this.handleResize();
      this.uiCallback("onSingleMonitor");
      if (!!onQuitUICallback) {
         onQuitUICallback();
         onQuitUICallback = null;
      }
   };

   this.onMultiMonitor = () => {
      this.uiCallback("onMultiMonitor");
   };

   this.onMonitorChanged = (settings, allReady, stillUpdating) => {
      JSCDKLogger.debug("monitor status changed");
      this.uiCallback("onMonitorChanged", {
         settings: settings,
         allReady: allReady,
         stillUpdating: !!stillUpdating
      });
   };

   this.switchToMultimon = function(wmksSession, sessionContainer, uiCallback,
      closeSidebar, handleResize, options) {
      if (!!multimonModel.wmksSession) {
         return;
      }
      this.closeSidebar = closeSidebar;
      this.uiCallback = uiCallback;
      this.handleResize = handleResize;
      monitorManageService.switchToMultiMonitor(wmksSession, sessionContainer,
         this.onQuitMultimon, this.onEnterMultimon, this.onSingleMonitor,
         this.onMultiMonitor, this.onTopologyChanged, this.onMonitorChanged,
         options);
      onQuitUICallback = null;
   };

   this.sendTopologyRequest = function() {
      let monitorSettings = multimonModel.monitorSettings,
         topologySettings = [],
         session = multimonModel.wmksSession;

      if (!session) {
         return;
      }

      // we can round the settings here.
      JSCDKLogger.info("set displayer settings to " + JSON.stringify(monitorSettings));

      multimonModel.forEachSettings(function(key, setting) {
         topologySettings.push({
            requestedWidth: setting.width,
            requestedHeight: setting.height,
            left: setting.x,
            top: setting.y
         });
      });
      session.wmksContainer.wmks('updateTopology', topologySettings, true);
   };

   this.switchToSingleMon = function(wmksSession) {
      if (!multimonModel.wmksSession ||
         wmksSession !== multimonModel.wmksSession) {
         return;
      }
      monitorManageService.switchToSingleMonitor();
   };

   this.destroyMultimon = function() {
      if (!multimonModel.wmksSession) {
         return;
      }
      monitorManageService.switchToSingleMonitor();
   };

   this.addMonitor = function(wmksSession, sessionContainer, uiCallback,
      closeSidebar, handleResize, options) {
      if (!multimonModel.wmksSession) {
         this.switchToMultimon(wmksSession, sessionContainer, uiCallback,
            closeSidebar, handleResize, options);
      } else if (wmksSession === multimonModel.wmksSession) {
         monitorManageService.addMonitor();
      }
   };

   this.whenNotInMultimon = function(processingFunction) {
      if (!multimonModel.wmksSession) {
         processingFunction();
         return;
      }
      let dialog = dialogService.confirm({
         data: {
            title: _('MM_QUIT_MULTIMON_T'),
            message: _('MM_QUIT_MULTIMON_M')
         },
         controller: ['$scope', function($scope) {
            $scope.confirm = function() {
               onQuitUICallback = function() {
                  $timeout(processingFunction);
               };
               monitorManageService.switchToSingleMonitor();
               dialog.close();
            };
            $scope.close = function() {
               JSCDKLogger.debug("cancel action to continue use multimon");
               dialog.close();
            };
         }]
      });
   };

   /**
    * Return the multimon using session
    * @return {string} return null if no session is using multimon
    */
   this.getMultimonSession = function() {
      if (!multimonModel.usingMultimon) {
         return null;
      }
      return multimonModel.wmksSession;
   };

   /**
    * Enter the multimon mode
    */
   this.enterMultimonMode = function() {
      if (!multimonModel.usingMultimon) {
         monitorManageService.enterMultimonMode();
      }
   };
}


