/**
 * app-htmlaccess.js
 * Entry for the whole html access app.
 * NOTE: this is not the entry for pdfjs and extended monitor page
 */

import angular from 'angular';
import 'jquery-ui';

// All css import here. DO NOT change their order.
import 'jqueryuiCss';
import 'webmksCss';
import 'toastrCss';
import 'ngdialogCss';

import './styles/style.css';
import './styles/appblast.css';
import './styles/clipboard.css';
import './styles/sidebar.css';
import './styles/modal-dialogs.css';


import templateModule from './template-module';
import commonModule from './common/module';
import launcherModule from './launcher/module';
import desktopModule from './desktop/module';
import {AppCenterController, config} from './app-center-controller';



// Define all controllers here for this module
export default angular.module('com.vmware.vdi.htmlaccess',
   [templateModule, commonModule, launcherModule, desktopModule])
   .run(['$window', config])
   .controller('AppCenterController',
      ['$scope', '$rootScope', 'constants', '$window', 'baseViewService',
         'connectionURIModel', AppCenterController])
   .name;




