/**
 * @fileoverview rtav-service.js -- RTAVService
 * Service implement RTAV feature for WebClient
 */

import RTAV from './logservice';
import './rtavconstants';
import './rtavsettings';
import DeviceMessageManager from './devicemessagemanager';
import dialogService from './dialogservice';
import SyncTimer from './synctimer';
import FrameRateController from './frameratecontroller';
import MessageWaitManager from './messagewaitmanager';
import AudioCapture from './audiocapture';
import VideoCapture from './videocapture';
import MediaCapture from './mediacapture';
import './typedarraysupport';
import MediaEncoder from './mediaencoder';
import resourceManager from './resourcemanager';
import rtavProtocolService from './rtavprotocolservice';
import RtavSessionController from './rtavsessioncontroller';
import RtavSession from './rtavsession';
import wmksAdapter from './wmksadapter';


RTAV.dialogService = dialogService;
RTAV.SyncTimer = SyncTimer;
RTAV.FrameRateController = FrameRateController;
RTAV.MessageWaitManager = MessageWaitManager;
RTAV.DeviceMessageManager = DeviceMessageManager;
RTAV.AudioCapture = AudioCapture;
RTAV.VideoCapture = VideoCapture;
RTAV.MediaCapture = MediaCapture;
RTAV.MediaEncoder = MediaEncoder;
RTAV.resourceManager = resourceManager;
RTAV.rtavProtocolService = rtavProtocolService;
RTAV.RtavSessionController = RtavSessionController;
RTAV.RtavSession = RtavSession;
RTAV.wmksAdapter = wmksAdapter;


let RTAVService = {
   onConnecting: RTAV.wmksAdapter.addSession,
   onConnected: RTAV.wmksAdapter.activeSession,
   onDisconnected: RTAV.wmksAdapter.removeSession,
   onDeviceStatusChanged: RTAV.resourceManager.onDeviceStatusChanged,
   setDialogHandler: RTAV.dialogService.setDialogFunction
};
if (Object.freeze) {
   RTAVService = Object.freeze(RTAVService);
}

export default RTAVService;


