import desktopTemplate from './common/desktop.html';


export default function routeConfig($routeProvider) {
   $routeProvider
      .when('/desktop', {
         templateUrl: desktopTemplate,
         reloadOnSearch: false,
         controller: 'DesktopCtrl'
      });
}



