export default function osModel() {
   function detectOS(platform) {
      let prefix = "download_rich_client_",
         os = platform || "unknown",
         getOSLiteral,
         mIsWin32 = false,
         mIsWin64 = false,
         mIsWinMobile = false,
         mIsLinux32 = false,
         mIsLinux64 = false,
         mIsMacOS = false,
         mIsIOS = false,
         mIsAndroid = false,
         mIsChromeOS = false,
         /*
          * Localization Map
          *   return the local key of the respective OS:
          *
          * win32 - os_win32
          * win64 - os_win64
          * winphone - os_winphone
          * linux32 - os_linux32
          * linux64 - os_linux64
          * mac - os_mac
          * android - os_android
          * ios - os_ios
          * chromeos - os_chromeos
          * unknown - os_unknown
          */
         osNames = {
            win32: 'os_win32',
            win64: 'os_win64',
            winmobile: 'os_winmobile',
            linux32: 'os_linux32',
            linux64: 'os_linux64',
            mac: 'os_mac',
            android: 'os_android',
            ios: 'os_ios',
            chromeos: 'os_chromeos',
            unknown: 'os_unknown'
         };

      switch (os) {
         case 'win32':
            mIsWin32 = true;
            break;
         case 'win64':
            mIsWin64 = true;
            break;
         case 'winmobile':
            mIsWinMobile = true;
            break;
         case 'linux32':
            mIsLinux32 = true;
            break;
         case 'linux64':
            mIsLinux64 = true;
            break;
         case 'mac':
            mIsMacOS = true;
            break;
         case 'android':
            mIsAndroid = true;
            break;
         case 'ios':
            mIsIOS = true;
            break;
         case 'chromeos':
            mIsChromeOS = true;
            break;
         default:
            break;
      }

      getOSLiteral = function(os) {
         return prefix + (osNames[os] || osNames.unknown);
      };

      /*
       * OS:
       *   win32, win64, linux32, linux64, mac, android, ios, chromeos, unknown
       */
      return {
         os: os,
         osLiteral: getOSLiteral(os),
         mIsWin32: mIsWin32,
         mIsWin64: mIsWin64,
         mIsWinMobile: mIsWinMobile,
         mIsLinux32: mIsLinux32,
         mIsLinux64: mIsLinux64,
         mIsMacOS: mIsMacOS,
         mIsIOS: mIsIOS,
         mIsAndroid: mIsAndroid,
         mIsChromeOS: mIsChromeOS
      };
   }

   return detectOS;
}




