import loginTemplate from './login/login.html';
import changePasswordTemplate from './login/change-password.html';
import securidNextTokenCodeTemplate from './login/securid-nexttokencode.html';
import securidPinchangeTemplate from './login/securid-pinchange.html';
import securidWaitTemplate from './login/securid-wait.html';
import launchitemsTemplate from './launchitems/launchitems.html';
import blastDesktopTemplate from './common/blast-desktop.html';
import warningTemplate from './common/home-warning.html';


export default function routeConfig($routeProvider) {
   $routeProvider
      .when('/', {
         templateUrl: loginTemplate,
         controller: 'LoginCtrl'
      })
      .when('/home', {
         templateUrl: loginTemplate,
         controller: 'LoginCtrl'
      })
      .when('/changepassword', {
         templateUrl: changePasswordTemplate,
         controller: 'ChangePasswordCtrl'
      })
      .when('/securidnexttoken', {
         templateUrl: securidNextTokenCodeTemplate,
         controller: 'SecuridNextTokenCodeCtrl'
      })
      .when('/securidpinchange', {
         templateUrl: securidPinchangeTemplate,
         controller: 'SecuridPinChangeCtrl'
      })
      .when('/securidwait', {
         templateUrl: securidWaitTemplate,
         controller: 'SecuridWaitCtrl'
      })
      .when('/launchitems', {
         templateUrl: launchitemsTemplate,
         controller: 'LaunchItemsCtrl'
      })
      .when('/blastdesktop', {
         templateUrl: blastDesktopTemplate,
         controller: 'BlastDesktopCtrl'
      })
      .when('/warning', {
         templateUrl: warningTemplate,
         controller: 'HomeWarningCtrl'
      })
      .otherwise({
         redirectTo: '/'
      });
}



